      ]
    }
  },
  methods: {
    selectTab (tabStatus) {
      this.tabStatus = tabStatus
    },
    changeFocusStatus (obj) {
      obj.isfocus = !obj.isfocus
    },
    addQuestion () {
      this.questionList.push({ title: '', content: '', isfocus: false })
    },
    deleteQuestion (obj) {
      var index = this.questionList.indexOf(obj)
      if (index !== -1) {
        this.questionList.splice(index, 1)
      }
    }
  }
}
